import {
  Container,
  loader,
  ScrollContainer,
  Sprite,
  resources,
  windowHeight,
  windowWidth
} from '@/constants';

import scene from '@/components/scene'
import navigation from '@/components/navigation'
import widgets from '@/components/widgets'

const stage = new Container()
// 加载控件
stage.init = () => {
  scene.init()
  widgets.init()

  widgets.x = (windowWidth - widgets.width) / 2
  widgets.y = windowHeight - widgets.height

  navigation.init()

  stage.event()
  stage.addChild(scene, widgets, navigation)
}

stage.event = function () {
  let cache = null;

  let isTopContent = false;
  let isBottomContent = false;

  this.interactive = true;
  /**
   * 
   * @param {*} o 
   * @param {*} n 
   * @returns Number 2,4,6,8,5
   */
  const isSlide = (o, n) => {
    console.log(n.timestamp - o.timestamp);
    if (n.timestamp - o.timestamp < 200) {
      const X = n.x - o.x;
      const Y = n.y - o.y;
      if (Math.abs(X) > Math.abs(Y) && X > 0) {
        return 6
      }
      else if (Math.abs(X) > Math.abs(Y) && X < 0) {
        return 4
      }
      else if (Math.abs(Y) > Math.abs(X) && Y > 0) {
        return 2
      }
      else if (Math.abs(Y) > Math.abs(X) && Y < 0) {
        return 8
      }
      else {
        return 5
      }
    }
    return false
  }

  const copyAddtimeStamp = (origin) => {
    let result = Object.assign({}, origin)
    result.timestamp = Date.now()
    return result
  }
  this.on('touchstart', (ev) => {
    cache = copyAddtimeStamp(ev.data.global)
  })

  this.on('touchend', (ev) => {
    let o = cache;
    let n = copyAddtimeStamp(ev.data.global);
    let direction = isSlide(o, n)
    if (direction) {
      let visible = navigation.visible
      if (visible && direction === 8) {
        navigation.visible = false
      } else if (!visible && direction === 2) {
        navigation.visible = true
      }
    }
    cache = null
    isTopContent = isBottomContent = false
  })




}

export default stage


